import React, { Component } from "react";
import "../scss/section.scss";
import Title from "../elements/Title/Title";
// import * as actionTypes from "../store/actions";
import { connect } from "react-redux";
import { Element } from "react-scroll";

class AboutUs extends Component {
  render() {
    return (
      <Element name="about">
        <div className="margin-80">
          <Title name="O NAS" />
          <div className="firstTextWidth textColor">
            <p className="">
              Poszujesz lokalu, gdzie nie bdziesz musia paci "od talerzyka"?
              Chciaaby/by zorganizowa:
            </p>
            <ul>
              <li>imprez,</li>
              <li>urdziny,</li>
              <li>spotkanie rodzinne,</li>
              <li>event biznesowy,</li>
              <li>konferencj?</li>
            </ul>
            <p className="">
              Jeli na ktre z tych pyta odpowiedziae tak, to trafie
              idealnie! Umw si i zobacz nowoczesny, luksusowy lokal tu w
              centrum Radomia! Wynajmujemy apartament o powierzchni 150m2, w
              ktrego w skad wchodz:
            </p>
            <ul>
              <li>1 azienka,</li>
              <li>VIP ROOM - pokj o powierzchni ... (za dodatkow opat),</li>
              <li>
                projektor (jeli bdziesz chcia/a, aby by razem z Wami podczas
                organizowanego wydarzenia pobierana jest dodatkowa kaucja),
              </li>
              <li>
                niezbdne wyposaenie, takie jak stoy, krzesa, czy wieszaki na
                ubrania.{" "}
              </li>
            </ul>
            <p>
              Soneczny Apartament znajduje si w Radomiu na Struga 18 (II
              pitro bez windy) tu obok Galerii Sonecznej. Wszystkich
              zainteresowanych zapraszamy serdecznie na spotkanie, w celu
              zobacznia oferowanego przez nas miejsca. Liczba osb, jak bdzie
              mg pomieci lokal to 50 osb.
            </p>
            <p>
              Masz wicej pyta? Chciaaby porozmawia o szczegach?
              Zapraszamy do kontaktu. Z chci odpowiemy na wszystkie pytania.
            </p>
          </div>
        </div>
      </Element>
    );
  }
}

const mapStateToProps = state => {
  return {};
};

const mapDispatchToProps = dispatch => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(AboutUs);
